
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { RPT205000111_REQ_BODY, OpenFixDepositReportDataType, RPT205000111_RES_BODY } from '@/shared/type/RPT205000111.type'
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";

const excel = new ExportExcel()

export default defineComponent({
    components: { Pagination, VueDatePicker },
    data() {
        return {
            openFixDepositReportData: [] as OpenFixDepositReportDataType[],
            COMMON_KEY_VALUE,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'Account Number ' },
                { columnName: 'Fix Deposit Type' },
                { columnName: 'Condition' },
                { columnName: 'Principle Amount' },
                { columnName: 'Currency' },
                { columnName: 'Tenor ' },
                { columnName: 'Maturity Date' },
                { columnName: 'Interest Rate' },
                { columnName: 'Payout Account' },
                { columnName: 'Remark' },
                { columnName: 'FD Account Status' },
                { columnName: 'Authorization Status code' },
                { columnName: 'Create By' },
                { columnName: 'Create Date ' },
                { columnName: 'Authorization By' },
                { columnName: 'Authorization Date' },
            ],
            totalItems: 0,
            pageSize: PAGE_SIZE,
            currentPage: 1,
            startingIndex: 1,
            isResetPage: false,
            searchValueBy: {
                date: '' as unknown,
            },
        }
    },
    mounted() {
        this.getOpenFixDepositReportData();
    },
    computed: {
        formattedDate(): string {
            return this.searchValueBy.date ? this.$datetime.formatVueDatePicker(this.searchValueBy.date as Date) : '';
        },
    },
    methods: {
        async getOpenFixDepositReportData() {
            const requestBody: RPT205000111_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: PAGE_SIZE,
                approvalRequestDate: this.searchValueBy.date ? this.$datetime.convertDateToString(this.searchValueBy.date as Date) : '',
            }
            const responseData = await this.$request.getRequest('RPT20500111', requestBody, false) as RPT205000111_RES_BODY;
            if (responseData.header.result) {
                this.openFixDepositReportData = this.onMappingTableData(responseData.body.list);
                this.totalItems = responseData.body.totalCount;
            }
        },

        async exportToExcel() {
            const customHeader = [
                "referenceNo",
                "accountNumber",
                "bizCid",
                "bizName",
                "fixDepositType",
                "condition",
                "principleAmount",
                "currency",
                "tenor",
                "maturityDate",
                "interestRate",
                "payoutAccount",
                "remark",
                "fdAccountStatus",
                "authorizationStatuscode",
                "createBy",
                "createDate",
                "authorizationBy",
                "authorizationDate"
            ]

            const reqBody = {
                approvalRequestDate: this.searchValueBy.date ? this.$datetime.convertDateToString(this.searchValueBy.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20500111", reqBody, false) as RPT205000111_RES_BODY;

            const tableForExport = this.onMappingTableData(res.body.list)

            const dataForExport: ExportSheet = {
                data: tableForExport,
                columnHeader: [customHeader],
                sheetName: 'Open Fix Deposit Report'
            }
            excel.exportSheet([dataForExport], 'Open Fix Deposit Report')
        },

        onMappingTableData(lists) {
            const { CorpBankingApprovalStatusCode, DepositRolloverDivideCode } = this.$store.state.CommonCodeModule.commonCode;
            return lists.map((item) => ({
                referenceNo: item.serverTransactionID,
                accountNumber: item.accountNo,
                bizCid: item.customerNo,
                bizName: item.customerName,
                fixDepositType: Number(item.contractMonths) <= 6 ? 'Short Term' : 'Long Term',
                condition: DepositRolloverDivideCode && item.depositRolloverDivideCode ? `${DepositRolloverDivideCode.find((type) => type.key == item.depositRolloverDivideCode)?.key}: ${DepositRolloverDivideCode.find((type) => type.key === item.depositRolloverDivideCode)?.value}` : '',
                principleAmount: this.$format.formatCurrency(Number(item.termDepositAmount), item.currencyCode),
                currency: item.currencyCode,
                tenor: item.contractMonths,
                maturityDate: item.maturityDate ? this.$datetime.formatStringToStringDate(item.maturityDate) : '',
                interestRate: item.applyInterestRate,
                payoutAccount: item.sourceOfFundAccountNo,
                remark: item.remark,
                fdAccountStatus: item.fDAccountStatus,
                authorizationStatuscode: CorpBankingApprovalStatusCode && item.corpBankingApprovalStatusCode ? `${CorpBankingApprovalStatusCode.find((type) => type.key == item.corpBankingApprovalStatusCode)?.key}: ${CorpBankingApprovalStatusCode.find((type) => type.key === item.corpBankingApprovalStatusCode)?.value}` : '',
                createBy: `${item.firstRegisterTellerID ? `(${item.firstRegisterTellerID})` : ''} ${item.firstRegisterTellerName ? item.firstRegisterTellerName : ''}`,
                createDate: `${item.firstRegisterDate ? this.$datetime.formatStringToStringDate(item.firstRegisterDate) : ''} ${item.firstRegisterTime ? this.$datetime.formatStringToTime(item.firstRegisterTime) : ''}`,
                authorizationBy: `${item.approverID ? `(${item.approverID})` : ''} ${item.approverName ? item.approverName : ''}`,
                authorizationDate: `${item.authorizationDate ? this.$datetime.formatStringToStringDate(item.authorizationDate) : ''} ${item.authorizationTime ? this.$datetime.formatStringToTime(item.authorizationTime) : ''}`,
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getOpenFixDepositReportData();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1;
                this.isResetPage = true;
                this.getOpenFixDepositReportData();
            } else {
                this.isResetPage = true;
                this.getOpenFixDepositReportData();
            }
        },

        handleReset() {
            this.searchValueBy.date = '';
            this.currentPage = 1;
            this.isResetPage = true;
            this.getOpenFixDepositReportData();
        }

    }
});

